body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

// ========================================

.container {
  box-sizing: border-box;
  max-width: 1124px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  align-items: center;
  // justify-content: center;
  margin: 20px 0;
  gap: 100px;
}

.img-card-1 {
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.75);
  padding: 2px;
  & img {
    width: 300px;
  }
}

.row-1 {
  padding-left: 25%;
  justify-content: left;
}

.row-2 {
  padding-right: 25%;
  justify-content: right;
}

.img-card-2 {
  padding: 2px;
  & img {
    max-width: 100px;
  }
}

.uznali {
  & h4 {
    font-size: 25px;
    font-weight: normal;
  }
}

.our-photo {
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.75);
  max-width: 30%;
  margin: 0 auto;
  & img {
    width: 100%;
    height: 100%;
  }
}

.timing {
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 50vw;
  // justify-content: center;
}

.timing-icon {
  flex-basis: 30%;
  & img {
    width: 80px;
  }
}

.timing-body {
  flex: 1 0 70%;
}

.timing-time {
  font-weight: bold;
}

.timing-desc {
  font-size: 14px;
}

.cnt-logo {
  max-width: 80px;
  max-height: 80px;
}

.w-date {
  font-style: italic;
  font-weight: normal;
  background-color: yellow;
  display: inline-block;
  padding: 8px;
  border-radius: 8px;
  font-size: large;
  margin: 5px auto 20px;
}

.h-italic {
  font-size: 45px;
  font-style: italic;
  font-weight: 500;
  margin: 30px auto;
  // font-family: Georgia, "Times New Roman", Times, serif;
  font-family: "Pacifico", serif;
}

.map-wrapper {
  position: relative;
  overflow: hidden;
}

.map-iframe {
  width: 560px;
  height: 400px;
  position: relative;
}
